<template>
    <el-card class="box-card">
        <div class="list_head" slot="header">
            <span>用户列表</span>
        </div>
        <el-table :data="tableData">
            <el-table-column prop="user_id" label="#" width="100">
            </el-table-column>
            <el-table-column prop="username" label="用户名">
            </el-table-column>
            <el-table-column prop="nickname" label="昵称">
            </el-table-column>
            <el-table-column prop="sex" label="性别">
            </el-table-column>
            <el-table-column prop="tel" label="手机">
            </el-table-column>
            <el-table-column prop="email" label="邮箱">
            </el-table-column>
            <el-table-column label="头像" width="180">
                <template slot-scope="scope">
                    <el-avatar shape="square" :src="scope.row.avatar">
                    </el-avatar>
                </template>
            </el-table-column>
            <el-table-column prop="usable" label="状态">
                <template slot-scope="scope">
                    <el-tag v-if="scope.row.usable != 1" type="info" plain disabled size="mini">禁用</el-tag>
                    <el-tag v-else type="success" plain size="mini">启用</el-tag>
                </template>
            </el-table-column>
            <el-table-column label="操作">
                <!-- template插槽 内部放复杂结构 必须加slot-scope="scope"-->
                <template slot-scope="scope">
                    <el-button v-permission="'user:list:switch'" @click="handleOpen(scope.row.user_id, scope.$index)"
                        :type="scope.row.usable === 1 ? 'info' : 'success'" icon="el-icon-switch-button" plain
                        size="mini">{{ scope.row.usable == -1 ? '启用' : '禁用'
                        }}</el-button>
                    <el-button v-permission="'user:list:remove'" @click="handleRemove(scope.row.user_id, scope.$index)"
                        plain size="mini" icon="el-icon-delete" type="danger">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
    </el-card>
</template>

<script>
import { User } from '@/api';

export default {
    data() {
        return {
            tableData: [],
        }
    },
    created() {
        this.loadList();
    },
    methods: {
        // 加载列表
        async loadList() {
            let { status, msg, data } = await User.list({ pagesize: 6 });
            if (status) {
                this.tableData = data;
            }
        },
        // 启用
        handleOpen(user_id, i) {
            this.$confirm(`此操作将${this.tableData[i].usable == 1 ? '禁用' : '启用'}该链接, 是否继续?,`, {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(async () => {
                // 另存了一下数据
                let usable = -this.tableData[i].usable;
                // 有个别数据 usable 为 0
                if (usable === 0) {
                    usable = 1;
                }
                let { status, msg } = await User.usable({ user_id, usable });
                if (status) {
                    this.tableData[i].usable = -this.tableData[i].usable;
                    // 信息提示
                    this.$message.success(msg);
                } else {
                    this.$message.error(msg);
                }
            }).catch(() => {
                this.$message.info('已取消启用');
            });
        },
        handleRemove(user_id, i) {
            // 查阅文档 -- $confirm 返回一个 promise 对象 ==> 所以用 .then .catch 分别捕获。
            // 也可以 使用 async await ==> try catch 捕获
            this.$confirm('此操作将永久删除该链接, 是否继续?', '确认删除', {
                confirmButtonText: '删除',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(async () => {
                // 删除链接 -- 需要 link_id ==>在scope.row中 ==> 所以传一个参数,即link_id
                let { status, msg } = await User.remove({ user_id });
                if (status) {
                    // 后台数据已经删除 -- 但是页面并未更新 -- 需要操作DOM -- 删除
                    this.tableData.splice(i, 1);
                    // 信息提示
                    this.$message.success(msg);
                } else {
                    this.$message.error(msg);
                }
            }).catch(() => {
                this.$message.info('已取消删除');
            });
        },
    }
}
</script>

<style lang="less" scoped>
.list_head {
    display: flex;
    justify-content: space-between;
}
</style>
